@include touch {
  .examples-block {
    padding: 60px 0 0;

    &__present {
      top: -67px;
      right: 38.2513661202%;
    }
  }

  .examples {
    //width: calc(100% + #{$wrapperPaddingTablets});

    .splide__arrows {
      right: $wrapperPaddingTablets;
    }
  }
}

@include mobile {
  .examples {
    width: 100%;
    max-width: 500px;
    margin: 0 auto;

    .dots {
      display: none;
    }
  }

  .examples-block__present {
    position: absolute;
    right: 33.125%;
    top: -48px;
  }

  .examples-item {
    height: calc(590px - 19vw);

    .examples-item__bottom {
      padding-bottom: 20px;
    }

    .examples-item__description {
      display: block;
      max-height: 100%;
      height: 100%;
      overflow: hidden;
    }

    .examples-item__logo {
      transform: translate(-50%, -50%) scale(0.5);
    }
  }
}
